<template>
  <v-app>
    <link rel="preconnect" href="https://fonts.googleapis.com">
    <link rel="preconnect" href="https://fonts.gstatic.com" crossorigin>
    <link href="https://fonts.googleapis.com/css2?family=Jua&display=swap" rel="stylesheet">
    <header id="open_animation">
      <div class="triangle left"><h1>넘어가요</h1></div>
      <div class="triangle right"><h1>넘어가요</h1></div>
    </header>
    <transition name="slide-fade" mode="out-in">
      <router-view />
    </transition>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    count: 0,
    page: 'pageAnimation',
    pagechang: '',
    right: null,
  }),
  mounted() {
    window.addEventListener('keyup', this.backPage);
    window.addEventListener('keydown', this.nextPage);
    document.addEventListener("DOMContentLoaded", this.pageTransition);
  },
  methods: {
    cancel() {
      this.count = 0
    },
    nextPage(e) {
      if (e.keyCode === 39) {
        this.count = this.count +1
        this.pagechang = this.page + this.count
        if (this.pagechang === "pageAnimation0") {
          this.$router.push(`/`)
          this.pageTransition()
        } else {
          this.$router.push(`${this.pagechang}`)
          this.pageTransition()
        }
      }
      
    },
    backPage(e) {
      if (e.keyCode === 37) {
        if (this.count <= 0) this.count = 1
        this.count = this.count - 1
        this.pagechang = this.page + this.count
        if (this.pagechang === "pageAnimation0") {
          this.$router.push(`/`)
          this.pageTransition()
        } else {
          this.$router.push(`${this.pagechang}`)
          this.pageTransition()
        }
      }
    },
    pageTransition() {
      document.getElementById("open_animation").classList.remove("open")
      setTimeout(() => { document.getElementById("open_animation").classList.add("open");},850);
    }
  },
};
</script>

