    top:0;
    left:0;
    width:100%;
    height:100%;
    background:linear-gradient(to top, 		#808080 0%, 	#C0C0C0 100%);
    transition:all .8s cubic-bezier(0.645, 0.045, 0.355, 1);
    display:flex;
    justify-content:center;
    align-items:center;
    pointer-events:none;
    
    &.left{
      clip-path: polygon(0 0, 0% 100%, 100% 100%);
    }
    
    &.right{
      clip-path: polygon(100% 0, 0 0, 100% 100%);
    }
  }
  
  &.open{
    
    .triangle{
      
      &.left{
        clip-path: polygon(0 0, 0 100%, 0 100%);
      }
      
      &.right{
        clip-path: polygon(100% 0, 100% 0, 100% 100%);
      }
    }
    
    button{
      left:40px;
      bottom:40px;
      width:50px;
      border-radius:50%;
      
      &:before{
        opacity:0;
        left:100%;
      }
      
      &:after{
        opacity:1;
        left:18px;
      }
      
      &.menu{
        width:100%;
        height:100%;
        bottom:0;
        left:0;
        border-radius:0;
        
        &:after{
          left:-100%;
        }
      }
    }
  }
}
</style>