import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import pageAnimation1 from '../views/pageAnimation1.vue'
// import pageAnimation2 from '../views/pageAnimation2.vue'
import transtion from '../components/pageTransitions.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/website.html',
    name: 'main',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/pageAnimation1',
    name: 'pageAnimation1',
    component: pageAnimation1
  },
  {
    path: '/transtion',
    name: 'transtion',
    component: transtion
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
