<template>
  <v-row justify="center">
    <v-date-picker v-model="picker"  @change="changeDate"  ></v-date-picker>
  </v-row>
</template>

<script>


  export default {
    data() {
      return {
        picker: null,
      }
    },
    created() {
      this.picker = this.$store.state.date.day
    },
    methods: {
      changeDate() {
        this.$store.dispatch('add', this.picker)
      }
    }
  }
</script>

